// frontend/src/components/UploadForm.js

import React, { useState } from 'react';
import { Button, CircularProgress, Typography, Box } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { uploadBloodSample } from '../utils/api';

const UploadForm = () => {
  const [file, setFile] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const navigate = useNavigate();

  const handleFileChange = (event) => {
    setFile(event.target.files[0]);
    setError('');
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    if (!file) {
      setError('Please select a file to upload.');
      return;
    }

    setLoading(true);
    try {
      console.log('Uploading file:', file.name);
      const response = await uploadBloodSample(file);
      console.log('Upload response:', response);
      if (response.error) {
        setError(response.error);
      } else {
        navigate('/results', { state: { prediction: response } });
      }
    } catch (error) {
      console.error('Upload error:', error);
      setError(`An error occurred while uploading the file: ${error.message}`);
    } finally {
      setLoading(false);
    }
  };

  return (
    <Box component="form" onSubmit={handleSubmit} sx={{ '& > *': { m: 1 } }}>
      <input
        type="file"
        accept=".pdf"
        onChange={handleFileChange}
        style={{ display: 'none' }}
        id="raised-button-file"
      />
      <label htmlFor="raised-button-file">
        <Button variant="contained" component="span">
          Choose File
        </Button>
      </label>
      {file && <Typography>{file.name}</Typography>}
      {error && <Typography color="error">{error}</Typography>}
      <Button
        type="submit"
        variant="contained"
        color="primary"
        disabled={!file || loading}
      >
        {loading ? <CircularProgress size={24} /> : 'Upload and Analyze'}
      </Button>
    </Box>
  );
};

export default UploadForm;